@import url(https://fonts.googleapis.com/css?family=Bungee+Shade|Roboto);
.App, .loading {
  text-align: center;
}

h1 {
  font-family: 'Bungee Shade', cursive;
  font-weight: 200;
  font-size: 64px;
  color: #FF0092;
  margin: 1%;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #313640;
  font-weight: bold;
  color: #FF0092;
}

/* credit to George-Payne's codepen for below grid */
.grid {
  top: -400px;
  bottom: -400px;
  left: -400px;
  right: -400px;
  position: absolute;
  z-index: -1;
  opacity: 0.5;
  background-color: #313640;
  background-image: linear-gradient(#FF0092 1px, transparent 2px),
  linear-gradient(90deg, #FF0092 1px, transparent 2px);
  background-size: 3% 3%, 3% 3%;
  background-position: 0 0, 0 0;
  -webkit-transform: perspective(300px) rotateX(80deg);
          transform: perspective(300px) rotateX(80deg);
}

.diffcultyBox {
  margin: 1% auto;
}

.submitBox {
  margin: 1% auto;
}

.submitInput {
  margin: 2px 1%;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  border: solid 1px transparent;
  border-radius: 4px;
  padding: 0.5em 1em;
  color: #ffffff;
  background-color: #FF0092;
  font-family: inherit;
  font-weight: bold;
  font-size: 16px;
  outline: none;
}

.submitInput:hover,
.submitInput:focus {
  color: #FF0092;
  background-color: white;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  outline: none;
}

.submitButton {
  margin: 2px 1%;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  border: solid 1px transparent;
  border-radius: 4px;
  padding: 0.5em 1em;
  color: #ffffff;
  background-color: #FF0092;
}

.submitButton:active {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  -webkit-filter: saturate(150%);
          filter: saturate(150%);
}

.submitButton:hover,
.submitButton:focus {
  color: #FF0092;
  background-color: white;
}

