.portalBeholderContainer {
  display: flex;
  justify-content: center;
}

.portalBox {
  width: 30%;
}

.beholderBox {
  width: 30%;
}

.portal {
  -webkit-animation: portal-spin infinite 20s linear;
          animation: portal-spin infinite 20s linear;
  width: 60%;
}

.beholder {
  width: 80%;
  -webkit-animation: emerge 5s;
          animation: emerge 5s;
}

.beholdertwo {
