@-webkit-keyframes portal-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes portal-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes emerge {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes emerge {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

